<template>
  <div class="input-container">
    <div class="type-radio">
      <i>Type:</i>
      <input type="radio" name="type" value="1" v-model="Type" />Type1
      <input type="radio" name="type" value="2" v-model="Type" />Type2
      <input type="radio" name="type" value="3" v-model="Type" />Type3
      <!-- <input v-for="(t,index) of types" type="radio" name="type" :value="t" v-model="Type">Type{{Type}} -->
      <br><br>
    </div>
    <div class="base-radio">
      <i>Base:</i>
      <input type="radio" name="base" value="2" v-model="Base" />二進位
      <input type="radio" name="base" value="8" v-model="Base" />八進位
      <input type="radio" name="base" value="10" v-model="Base" />十進位
      <input type="radio" name="base" value="16" v-model="Base" />十六進位
      <input type="radio" name="base" />其他進位:<input
        type="number"
        name="base"
        placeholder="輸入2-36進位"
        v-model="Base"
      />
      <!-- <input v-for="(b,index) of bases" type="radio" name="base" :value="b" v-model="Base">{{Base}}進位
                                 -->
      <br><br>
    </div>
    <i>Limit:</i
    ><input
      type="number"
      placeholder="Limit"
      v-model.number.trim="Limit"
    /><br /><br />
    <i>InputA:</i
    ><input type="text" placeholder="輸入A" v-model.trim="A" /><br /><br />
    <i>InputB:</i
    ><input type="text" placeholder="輸入B" v-model.trim="B" /><br /><br />
    <div class="btn-container">
      <button>try_catch</button>
      <button @click="reset">Reset</button>
      <button @click="Random">Random</button>
    </div>
    <div class="showup">
      <h1>Case:{{ CaseChoose }}</h1> 
      <h3>Type:{{ Type }}</h3>
      <h3>Base:{{ Base }}</h3>
      <h3>Limit:{{ Limit }}</h3>
      <h3>A:{{ A }}</h3>
      <h3>B:{{ B }}</h3>
      <h3>DecA:{{ DecA }}</h3>
      <h3>DecB:{{ DecB }}</h3>
      <h1>Result:{{ Result }}</h1>
      <h1>DecResult:{{ Dec_result }}</h1>
      <h1 v-show="error">Error!</h1>
      <br />
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeWork01",
  data() {
    return {
      Type: "1",
      Base: "2",
      Limit: "4",
      count: "0",
      CaseChoose: "1",
      A: "0010",
      B: "0100",
      error: false,
      types: [1, 2, 3],
      bases: [2, 8, 10, 16],
    };
  },
  methods: {
    TwoSum(arrayA, arrayB, Base) {
      // The arrayA has same length with arrayB
      this.count = 0;
      let length = arrayA.length;
      let sum = 0;
      let temp_array = arrayA;
      for (let i = length - 1; i >= 0; i--) {
        sum =
          parseInt(arrayA[i], Base) + parseInt(arrayB[i], Base) + this.count;
        // c(parseInt(arrayA[i], Base) + parseInt(arrayB[i], Base))
        if (sum >= Base) {
          sum %= Base;
          this.count = 1;
        } else this.count = 0;
        temp_array[i] = sum.toString(Base).toUpperCase();
      }
      return temp_array;
    },
    Ones(array, Base) {
      let temp_array = array;
      for (let i = 0; i < array.length; i++) {
        temp_array[i] = parseInt(array[i], Base);
        temp_array[i] = (Base - 1 - temp_array[i]).toString(Base).toUpperCase();
      }
      return temp_array;
    },
    Twos(array, Base, Limit) {
      let temp_array = this.Ones(array, Base);

      let twos_array = this.TwoSum(
        temp_array,
        this.add1("1".split(""), Limit - 1),
        Base
      );

      return twos_array;
    },
    showup(type, str, base, limit) {
      // c(str)
      let t1 = parseInt(str.slice(1), base)
      let t2 = parseInt(
        this.Ones(str.slice(1).split(""), base, limit).join(""),
        base
      )
      let t3 = parseInt(
        this.Twos(str.slice(1).split(""), base, limit).join(""),
        base
      )
      if (/^0|^\+/.test(str)) {
        if(t1===0)
          return 0
        return "+" + t1;
      } else {
        switch (type) {
          case "1":
            if(t1===0)
              return 0
            return "-" + t1;
          case "2":
            if(t2===0)
              return 0
            return "-" +t2 ;
          case "3":
            if(t3===0)
              return 0
            return "-" +t3
            
        }
      }
    },

    delsign(str, base) {
      if (/^-/.test(str))
        return str.replace(/^-/, (Number(base) - 1).toString(base));

      return str.replace(/^\+/, "0");
    },
    add1(array, Limit) {
      while (array.length < Limit) {
        array.unshift("0");
      }

      return array;
    },

    add0(array, Limit) {
      let sign = array[0];
      let temp_array = array;
      temp_array[0] = "0";
      while (temp_array.length < Limit) {
        temp_array.unshift("0");
      }

      temp_array[0] = sign;

      return temp_array;
    },
    Caculate(Type, Base, Limit, InputA, InputB) {
      InputA = this.delsign(InputA, Base);
      InputB = this.delsign(InputB, Base);

      let A_array = InputA.split("");
      let B_array = InputB.split("");

      let Case_Choose = CaseChoose(Type, ABsign());

      A_array = this.add0(A_array, Limit);
      B_array = this.add0(B_array, Limit);

      return this.Outcome(Case_Choose, A_array, B_array, Base, Limit);

      // return Twos

      function Ispositive(string) {
        return string[0] === "0" ? true : false;
      }
      function ABsign(A = Ispositive(InputA), B = Ispositive(InputB)) {
        if (A === B && A === true && B === true) return "1";
        else if (A !== B) return "2";
        else return "3";
      }
      function CaseChoose(Type, ABsign) {
        switch (Type) {
          case "1":
            switch (ABsign) {
              case "1":
                return "1";
              case "2":
                return "2";
              case "3":
                return "1";
            }
            break;
          case "2":
            switch (ABsign) {
              case "1":
                return "3";
              case "2":
                return "4";
              case "3":
                return "5";
            }
            break;
          case "3":
            switch (ABsign) {
              case "1":
                return "3";
              case "2":
                return "6";
              case "3":
                return "7";
            }
            break;
        }
      }
    },
    Outcome(Case_Choose, A_array, B_array, Base, Limit) {
      this.error = false;
      switch (Case_Choose) {
        case "1": {
          //只取大小做相加
          this.CaseChoose = "1";
          console.log("now case1");
          let sum = [];
          let A_slice_array = A_array.slice(1);
          let B_slice_array = B_array.slice(1);
          sum = this.TwoSum(A_slice_array, B_slice_array, Base);
          //判斷是否溢位
          if (this.count === 1) {
            sum.unshift((Base - 1).toString(Base).toUpperCase());
            this.error = true;
            return sum;
          }

          sum.unshift(A_array[0]);
          return sum;
        }
        case "2": {
          //將A的大小 與 B的大小取1's 相加
          this.CaseChoose = "2";

          console.log("now case2");
          let sum = [];
          // let array = ['1']
          // let add1_array = add1(array, Limit)
          let A_slice_array = A_array.slice(1);
          let B_slice_array = B_array.slice(1);
          B_slice_array = this.Ones(B_slice_array, Base);
          sum = this.TwoSum(A_slice_array, B_slice_array, Base);

          if (this.count === 1) {
            sum = this.TwoSum(sum, this.add1("1".split(""), Limit - 1), Base);
            sum.unshift(A_array[0]);
          } else {
            sum = this.Ones(sum, Base);
            sum.unshift(B_array[0]);
          }
          return sum;
        }
        case "3": {
          //輸入為b-1補數

          //連同符號位元一起相加
          this.CaseChoose = "3";

          console.log("now case3");
          let sum = [];
          sum = this.TwoSum(A_array, B_array, Base);

          //判斷是否溢位
          if (sum[0] !== "0") this.error = true;

          return sum;
        }
        case "4": {
          //輸入為b-1補數
          this.CaseChoose = "4";

          console.log("now case4");
          let sum = [];
          sum = this.TwoSum(A_array, B_array, Base);
          if (this.count === 1) {
            sum = this.TwoSum(sum, this.add1("1".split(""), Limit), Base);
          }
          return sum;
        }
        case "5": {
          //輸入為b-1補數
          this.CaseChoose = "5";

          console.log("now case5");
          let sum = [];
          sum = this.TwoSum(A_array, B_array, Base);
          sum = this.TwoSum(sum, this.add1("1".split(""), Limit), Base);

          if (sum[0] !== B_array[0]) {
            this.error = true;
          }
          return sum;
        }
        case "6": {
          this.CaseChoose = "6";

          //輸入為b補數
          console.log("now case6");
          let sum = [];
          sum = this.TwoSum(A_array, B_array, Base);
          return sum;
        }
        case "7": {
          //輸入為b補數
          this.CaseChoose = "7";

          console.log("now case7");
          let sum = [];

          sum = this.TwoSum(A_array, B_array, Base);
          if (sum[0] !== B_array[0]) {
            this.error = true;
          }
          return sum;
        }
      }
    },
    Random() {
      let base = [2, 8, 10, 16];
      let temp_type = Math.floor(Math.random() * 3 + 1).toString();
      this.Type = temp_type;
      let temp_base = base[Math.floor(Math.random() * 4)].toString();

      this.Base = temp_base;
      let temp = Math.floor(Math.random() * 9 + 2).toString();
      this.Limit = temp;
      let temp_arrayA = [];
      let arr = [
        0,
        Number(temp_base - 1)
          .toString(temp_base)
          .toUpperCase(),
      ];
      for (let i = 0; i < temp - 1; i++) {
        temp_arrayA.unshift(
          Math.floor(Math.random() * temp_base)
            .toString(temp_base)
            .toUpperCase()
        );
      }
      temp_arrayA.unshift(arr[Math.floor(Math.random() * 2)]);
      this.A = temp_arrayA.join("");

      let temp_arrayB = [];
      for (let i = 0; i < temp - 1; i++) {
        temp_arrayB.unshift(
          Math.floor(Math.random() * temp_base)
            .toString(temp_base)
            .toUpperCase()
        );
      }
      temp_arrayB.unshift(arr[Math.floor(Math.random() * 2)]);
      this.B = temp_arrayB.join("");
    },
    
    reset() {
      this.Type = "1";
      this.Base = "2";
      this.Limit = "4";
      this.A = "0001";
      this.B = "0110";
    },
  },
  computed: {
    Result() {
      let text =
        "^" + (this.Base - 1).toString(this.Base).toUpperCase() + "|^0|^-|^\\+";
      let reg = new RegExp(text);

      if (
        reg.test(this.A) &&
        reg.test(this.B) &&
        this.A.length <= this.Limit &&
        this.B.length <= this.Limit
      )
        return this.Caculate(
          this.Type,
          this.Base,
          this.Limit,
          this.A,
          this.B
        ).join("");
      else return "非標準輸入格式";
    },
    DecA() {
      return this.showup(this.Type, this.A, this.Base, this.Limit);
    },
    DecB() {
      return this.showup(this.Type, this.B, this.Base, this.Limit);
    },
    Dec_result() {
      return this.showup(this.Type, this.Result, this.Base, this.Limit);
    },
  },
  watch: {
    Base(){
      
    }
  },
};
</script>

